@import '../base/variables'
@import '../base/typography'
    
    
.skill
    display: flex
    flex-direction: column
    align-items: center
    text-align: center
    min-width: 240px
    max-width: 500px
    width: 100%
    margin: 10px
    gap: 0.5em

    .skill-image
        width: 300px
        height: 300px
        object-fit: cover

    .skill-title
        margin: 10px 0 10px 0
        font-size: 24px
        font-weight: bold

    .skill-description
        margin-top: 5px
        font-size: 16px
        color: $granite-color

    .skill-keywords
        margin: 0.5em 1.5em 0 1.5em

        .skill-keyword
            display: inline-block
            margin: 5px
            padding: 5px 10px
            background-color: $light-purple-color
            color: $granite-color
            font-size: 14px
            border-radius: 4px
